import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import "./header.css"

const Header = (props) => (
  <header
    style={{
      marginBottom: `0rem`,
      textAlign:"center"
    }}
    
    sx={{
      // this uses the value from `theme.space[4]`
      padding: 4,
      // these use values from `theme.colors`
      color: "background",
      backgroundColor: "primary",
    }}
  >
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `1.45rem 1.0875rem`,
      }}
    >
      <h1
       style={{ margin: 0 }}
      >
        <Link
          to="/"
          style={{
            color: `white`,
            textDecoration: `none`,
          }}
        >          
   <title>{props.siteTitle}</title>
   <meta className="swiftype" name="title" data-type="string" content={`${props.siteTitle}`} />
   <meta class="swiftype" name="description" data-type="text" content={`${props.siteDescription}`} />
   {/* <meta class="swiftype" name="tags" data-type="string" content={`${}`} />
   <meta class="swiftype" name="tags" data-type="string" content={`${}`} /> */}

        </Link>
      </h1>
    </div>
  </header>
)

