import React from "react"
import Link from "gatsby-link"
import "../scss/routerLinks.scss"
import {StaticQuery, graphql} from "gatsby"
 export default () => (
   
  <StaticQuery
  query={graphql`{
    contentstackGatsbyHeader {
      group {
        icon
        link {
          href
          title
        }
      }
    }
  }`}
  render={data=>(
    <nav >
     <ul className="routerLinks-navUl"
    >
      
{data.contentstackGatsbyHeader.group.map((el,i)=>
  <li key={i}>
    <Link className="routerLinks-navLi" to={`${el.link.href}`}>
    {el.link.title}
    </Link>
    </li>
)}
    </ul> 
    </nav>
 
  )}
  />
  
)
